html {
    background-color: #111;
  body {
    color: #fff;
    .logo {
        animation: pulse 3s ease 0s infinite normal none;
        animation-iteration-count: 1;
        padding: .5em 1.25em;
        height: 12vh;
    }
    .tagline {
        float: right;
        padding: 1em 2em;
        font-size: 5vh;
    }
    .video-container {
        position: relative;
        width: 100%;
        padding-bottom: 56.25%; /* 16:9 aspect ratio (height/width) */
        overflow: hidden;
      
        .video-wrapper {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        
          iframe {
            width: 100%;
            height: 100%;
          }
        }
      }

    .footer {
        ul {
            list-style: none;
            padding-left: 1em;
        }
    }
  }
}
